* {
  border: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

.name {
  color: #ff985c !important;
}

.btn {
  background-color: #ff985c !important;
  border: none;
}

.abs {
  position: absolute;
}

.content {
  width: 100%;
  height: auto;
}

li {
  font-size: 33px;
  list-style: none;
  text-decoration: none;
  color: black;
}

h2 {
  color: white;
  margin-left: 280px;
}

.ImgW {
  width: 380px;
  height: 250px;
  object-fit: scale-down;
}

.ImgW img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.modalImg {
  width: 380px;
  height: 250px;
}

.modalImg img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.res {
  width: 1600px !important;
  height: 100% !important;
  border: 1px solid brown;
}
